.Button {
  width: 88px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  cursor: pointer;
  background-color: transparent;
  border: none;
  color: #fff;
  font-family: Open Sans;
  outline: none;
  height: 100%;
}

.Button:hover {
  background-color: #3c5fa5;
}

.label {
  background: #ff5e5e;
  position: absolute;
  min-width: 12px;
  height: 12px;
  font-family: Open Sans;
  font-size: 8px;
  font-weight: 700;
  border-radius: 40%;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid #4267b2;
  top: 4px;
  left: 29px;
}

.icon {
  width: 24px;
  height: 24px;
}

.text {
  margin: 0;
  padding: 0;
  font-family: Open Sans;
  font-size: 12px;
  font-weight: 400;
}

@media screen and (max-width: 510px) {
  .Button {
    display: none;
  }
}
